.herotext {
    justify-content: center;
    margin-top: 40px;
    font-size: large;
    color:cadetblue;
}
.herotext h1 {
    font-size: 60px;
    margin-left: 25%;
    margin-right: 25%;
}
.herotext h3 {
    margin-left: 20%;
    margin-right: 15%;
    justify-content: space-evenly;
    
}
.herotext button {
    font: inherit;
    border: 1px solid #315052;
    background: #315052;
    color: white;
    padding: 0.75rem 3.5rem;
    cursor: pointer;
    font-size: 1.5rem;
    border-radius: 10px;
    margin-left: 35%;
    margin-top:30px;
    margin-bottom: 60px;
  }