.textComponent {
    justify-content: center;
    margin-left: 60px;
    margin-top: 20px;
    margin-right: 40px;
}
.textComponent p {
    margin-top: 20px;
    font-size: 16px;
}
.textComponent h1 {
    margin-top: 20px;
    color:lightslategray;
}
.textComponent button {
    font: inherit;
    border: 1px solid #4f005f;
    background: #4f005f;
    color: white;
    padding: 0.75rem 3.5rem;
    cursor: pointer;
    font-size: 1.15rem;
    border-radius: 30px;
    margin-bottom: 60px;
  }

